}
.boton-amarillo:hover {
  background-color: #c77808;
}
.boton-amarillo:active {
  background-color: #71B100;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1rem;
  display: block;
  justify-content: center;
  align-items: center;
  width: 22rem;
  box-shadow: 1px 2px 4px #5a8d00;
}
.boton-verde:hover {
  background-color: #619800;
}
.boton-verde:active {
  background-color: #71B100;
}

.btoContacto {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35rem;
}
.btoContacto:hover {
  background-color: #c77808;
}
.btoContacto:active {
  background-color: #71B100;
}

.verTodas {
  display: flex;
  justify-content: center;
  align-items: center;
}

footer {
  background-color: gray;
  margin-top: 5rem;
}

@media (max-width: 768px) {
  footer > nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.contacto {
  background-image: url("/src/img/encuentra.jpg");
  background-position: center center;
  background-repeat: no-repeat;
