@media (max-width: 768px) {
  .blogYTestimonios {
    display: flex;
  }
  .testimonios {
    display: none;
  }
  .blogContainer {
    width: 30rem;
    height: 55rem;
    display: flex;
    flex-direction: column;
  }
  .blogTexto {
    width: 25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 34rem;
  }
  .imgBlog {
    width: 25rem;
    height: 15rem;
  }
  .tituloBlog {
    font-size: 2rem;
    font-weight: bold;
  }
  .parrafoCreador {
    font-size: 1.5rem;
  }
