@media (min-width: 768px) {
  .hamburguesa {
    display: none;
  }
}

@media (max-width: 480px) {
  .logo > h1 {
    font-size: 2.5rem;
    display: flex;
  }
  .logo {
    width: 100%;
    display: flex;
    justify-content: space-around;
  }
  .containerPropiedad {
    width: 100%;
  }
}
@media (min-width: 480px) {
  .logo {
    display: flex;
    justify-content: space-around;
    width: 100%;
  }
}
@media (min-width: 768px) {
  .logo {
    width: 40rem;
  }
}

@media (max-width: 780px) {
  .navegacion {
    width: 100%;
    height: 38rem;
    visibility: hidden;
    opacity: 0;
    transition: visibility 1s ease-in-out, opacity 0.5s linear;
  }
  .navegacion > ul > li {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.399);
  }
  .navegacion > ul > li:hover {
    background-color: rgba(10, 10, 90, 0.432);
  }
  .mostrar {
    visibility: visible;
    opacity: 1;
  }
  .mostrar > ul > li :hover {
    text-shadow: 1px 2px 22px rgb(114, 181, 239);
  }
}
.cuadrado {
  width: 5rem;
  height: 5rem;
  background-color: blue;
  position: absolute;
  animation: moveRight 1s ease-in-out 1s;
}

@keyframes moveRight {
  from {
    height: 0;
  }
  to {
    height: 10rem;
  }
  to {
    height: 3rem;
  }
  to {
    height: 40rem;
  }
}
@keyframes changeColor {
  10% {
    background-color: red;
  }
  40% {
    background-color: violet;
  }
  70% {
    background-color: rgb(47, 217, 255);
  }
}
.bdyAnim {
  padding: 3rem;
}

.circulo {
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  background-color: red;
}